    getDestinationDetails() {
        this.getDestinationMethod();
        this.showSpinnerForAction = true;
        this.masterReadService.getDestinationForLREntryService(this.lrDtoDestination).subscribe(
            (response) => {
                if (response.length > 0) {
                    this.destinationOptions = response;
                    this.destinationTA = [];
                    for (let i = 0; i < this.destinationOptions.length; i++) {
                        this.destinationTA.push(this.destinationOptions[i]);
                    }
                    this.showSpinnerForAction = false;
                } else {
                    this.destinationOptions = [];
                    this.destinationTA = [];
                    this.showSpinnerForAction = false;
                }
            }), (error) => {
                this.showSpinnerForAction = false;
                swal("Error", "Server Problem Occurred While getting the Source Details", "info");
            },
            () => console.log('done');
    };

    validateSearchBtn() {
        this.validateViewTypeId = $("#viewType").val();

        if (this.cbSourceView == true) {
            this.sourceModeMtd();
        } else {
            this.destinyModeMtd();
        }
    }

    sourceModeMtd() {
        if (this.hideshowSourceDropDown == true) {
            if (($("#sourceId").val() == null) || ($("#sourceId").val() == undefined) ||
                ($("#sourceId").val() == "") || ($("#destinationId").val() == null) ||
                ($("#destinationId").val() == undefined) || ($("#destinationId").val() == "")) {
                swal("Mandatory Field", "Source & Destination is Mandatory Field", "warning");
                return false;
            } else {
                this.searchMtd();
            }
        } else {
            if (($("#destinationId").val() == null) || ($("#destinationId").val() == undefined) ||
                ($("#destinationId").val() == "")) {
                swal("Mandatory Field", "Destination is Mandatory Field", "warning");
                return false;
            } else {
                this.searchMtd();
            }
        }
    }

    destinyModeMtd() {
        if (($("#destinationId").val() == null) || ($("#destinationId").val() == undefined) ||
            ($("#destinationId").val() == "")) {
            swal("Mandatory Field", "Destination is Mandatory Field", "warning");
            return false;
        } else {
            this.searchMtd();
        }
    }

